<template>
  <swiper>
	 <swiper-item v-for='item in banners'>
	  <a :href='item.link'>
	    <img :src='item.image' alt='' @load='imageLoad'>
	  </a>
	</swiper-item>
  </swiper>
</template>

<script>
	import {Swiper, SwiperItem} from 'components/common/swiper'
	
	export default {
		name: "HomeSwiper",
		props: {
		  banners: {
		    type: Array,
			default() {
			  return []
			}
		  }		  
		},
		data() {
		  return {
				isLoad: false
			}								
		},
		components: {
		  Swiper,
		  SwiperItem
		},
		methods : {
		  imageLoad() {
				//只需發送一次,所以之後改為true
			  if(!this.isLoad) {
					this.$emit('swiperImageLoad')
					this.isload = true
				}
		  }
		}
	}
</script>

